import React, { FunctionComponent } from 'react'
import Header from 'components/Header'
import TopContainer from 'components/TopContainer'
import RoomExplain from 'components/RoomExplain'
import SlideFull from 'components/SlideFull'
import MainRoom from 'components/MainRoom'
import Reservation from 'components/Reservation'
import Footer from 'components/Footer'

const features = [
  { name: '최대 숙박인원', description: '최대 4인 (기본 2인, 인당 2만원추가)' },
  {
    name: '침대 갯수',
    description: '싱글사이즈 2개',
  },
  {
    name: '부대 시설',
    description: '냉장고, 전기 주전자, 커피 및 차, 무료 Wi-Fi, TV',
  },
  {
    name: '어메니티',
    description: '샴푸, 컨디셔너, 바디워시, 비누, 칫솔, 치약',
  },
  {
    name: '방 특징',
    description: '편안한 분위기와 모던한 인테리어',
  },
  {
    name: '가격',
    description: '예약사이트 참고',
  },
]

const roomImages = [
  { src: '/img/roombg1.jpg', alt: 'Image1' },
  { src: '/img/roombg2.jpg', alt: 'Image2' },
  { src: '/img/roombg3.jpg', alt: 'Image3' },
  { src: '/img/roombg4.jpg', alt: 'Image4' },
  { src: '/img/roombg5.jpg', alt: 'Image5' },
  { src: '/img/roombg6.jpg', alt: 'Image6' },
]

const roomExplainImages = [
  { src: '/img/roombg7.jpg', alt: 'Image1' },
  { src: '/img/roombg8.jpg', alt: 'Image2' },
  { src: '/img/roombg9.jpg', alt: 'Image3' },
  { src: '/img/roombg10.jpg', alt: 'Image4' },
]

const IndexPage: FunctionComponent = function () {
  return (
    <>
      <Header />
      <TopContainer h1Content="A-TYPE 룸" pContent="ROOM > A-TYPE 룸" />
      <SlideFull images={roomImages} />
      <RoomExplain
        title="A TYPE 룸"
        description="편안한 분위기와 모던한 인테리어가 돋보이는 A타입 룸"
        features={features}
        images={roomExplainImages}
      />
      <MainRoom />
      <Reservation bgSrc="/img/sectionbg7.png" />
      <Footer />
    </>
  )
}

export default IndexPage
