import React from 'react'
import styled from '@emotion/styled'

// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react'
import { Autoplay, EffectFade } from 'swiper'

// Import Swiper styles
import 'swiper/css'
import 'swiper/css/pagination'
import 'swiper/css/navigation'
import 'swiper/css/effect-fade'

const MainSlideStyle = styled.div`
  margin-top: 2rem;

  .swiper {
    width: 100%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;

    .swiper-pagination-bullet {
      width: 12px;
      height: 12px;
      background: rgba(255, 255, 255, 0.5);
      opacity: 1;
    }

    .swiper-pagination-bullet-active {
      width: 40px;
      transition: width 0.5s;
      border-radius: 5px;
      background: #000;
      border: 1px solid transparent;
    }

    .swiper-button-next,
    .swiper-button-prev {
      background-color: rgba(255, 255, 255, 0.5);
      color: #000;
      opacity: 1;
    }

    .swiper-button-next::after,
    .swiper-button-prev::after {
      font-size: 25px;
    }
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .slide-content {
    position: absolute;
    color: white;
    text-align: left;
    max-width: 50%;
    margin: 2.5rem;
  }

  .swiper-slide[data-slide-number='1'] .slide-content,
  .swiper-slide[data-slide-number='3'] .slide-content,
  .swiper-slide[data-slide-number='5'] .slide-content {
    left: 0;
    padding-left: 1rem;
  }

  .swiper-slide[data-slide-number='2'] .slide-content,
  .swiper-slide[data-slide-number='4'] .slide-content,
  .swiper-slide[data-slide-number='6'] .slide-content {
    right: 0;
    padding-right: 1rem;
    text-align: right;
  }

  .slide-content h2 {
    font-size: 3rem;
    margin-bottom: 0.625rem;
  }

  .slide-content p {
    font-size: 2rem;
  }

  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @media (max-width: 480px) {
    .swiper-slide img {
      height: 300px;
    }

    .slide-content {
      margin: 1.5rem;
    }

    .slide-content h2 {
      font-size: 2rem;
    }

    .slide-content p {
      font-size: 1.5rem;
    }
  }

  @media (min-width: 481px) and (max-width: 768px) {
    .swiper-slide img {
      height: 400px;
    }

    .slide-content {
      margin: 2rem;
    }

    .slide-content h2 {
      font-size: 2rem;
    }

    .slide-content p {
      font-size: 1rem;
    }
  }

  @media (min-width: 769px) and (max-width: 1024px) {
    .swiper-slide img {
      height: 600px;
    }
  }

  @media (min-width: 1025px) {
    .swiper-slide img {
      height: 800px;
    }
  }
`

type ImageData = {
  src: string
  alt: string
}

type ExampleProps = {
  images: ImageData[]
}

// import required modules
import { Pagination, Navigation } from 'swiper'

export default function Example({ images }: ExampleProps) {
  return (
    <MainSlideStyle>
      <Swiper
        effect={'fade'}
        slidesPerView={1}
        spaceBetween={30}
        loop={true}
        autoplay={{
          delay: 3000,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Pagination, Navigation, Autoplay, EffectFade]}
      >
        {images.map((image, index) => (
          <SwiperSlide data-slide-number={index + 1}>
            <img src={image.src} alt={image.alt} />
          </SwiperSlide>
        ))}
      </Swiper>
    </MainSlideStyle>
  )
}
