import React from 'react'

interface ExampleProps {
  h1Content: string
  pContent: string
}

const Example: React.FC<ExampleProps> = ({ h1Content, pContent }) => {
  return (
    <>
      <main className="relative isolate min-h-full">
        <img
          src="/img/sectionbg4.png"
          alt="sectionbg3"
          className="absolute inset-0 -z-10 h-full w-full object-cover object-top"
        />
        <div className="mx-auto max-w-7xl p-4 text-center sm:py-24 lg:px-8">
          <h1 className="mt-4 text-3xl font-bold tracking-tight text-white sm:text-5xl">
            {h1Content}
          </h1>
          <p className="mt-4 text-sm text-white/70 sm:mt-6 sm:text-lg">
            {pContent}
          </p>
        </div>
      </main>
    </>
  )
}

export default Example
